@import "../layout/style";
@import "../components/icon/pure-css-icon";
@import "../components/icon/icon";
@import "../components/modal";

@import "../layout/jx/jx-card";
@import "../layout/jx/jx-card-side";
@import "../layout/jx/jx-list";
@import "../layout/jx/jx-talent-list";
@import "../layout/jx/jx-chatbox";
@import "../layout/position/header";
@import "../layout/position/description";
@import "../layout/position/company";


/* 职位描述页面公司详情 */

.info-item-line {
  height: $card-pic-container-size/2;
  width: 100%;
  padding: 4px 0;
}

/* 职位邀请块样式 */
.input-container {
  padding-bottom: 16px;
  textarea {
    background-color: #FAFAFA;
    border: none;
    border-radius: 0;
    outline: none;
    height: 38px;
    width: 100%;
    resize: none;
    margin: 0 0 24px 0;
    padding: 8px;
    box-shadow: none;

  }
}

.section-card {

  .company-info,
  .company-job{
    padding-top: 24px;
    p,label,span {
      display: inline-block;
      vertical-align: top;
      @extend .invitation-content-font;
    }

  }

  /* ————————————————————————————————————————————————————————————————————————————————— */
  /* 上面的样式基本可以提取出来 */

  .content-item {
    height: $content-list-height;
    width: 100%;
    position: relative;

    .item-title {
      position: absolute;
      display: inline-block;
      width: $content-list-title-width;
      text-align: right;
      /* 保证和右侧下拉框对齐 */
      top: 8px;
    }

    .item-content {
      position: absolute;
      width: 100%;
      height: $content-list-height;
      padding-left: $content-list-title-width + 16px;;
      .select-wrapper input.select-dropdown {
        color: rgba(0,0,0,0.54);

        .dropdown-content > li {
          a, span {
            color: rgba(0,0,0,0.54);
          }

        }
      }
      p {
        /* 同title中的h1一致 */
        line-height: 20px;
        padding-top: 8px;

        b {
          font-size: 14px;
          color: $primary-color;
        }
      }
    }
  }

  .content-main {
    @include border-radius(2px);
    border: 2px solid #E6E6E6;
    background: $gray-header-color;
    padding: 8px;
    p {
      line-height: 20px;
      color: rgba(0,0,0,0.28);
    }
  }

  .content-action {
    height: $section-content-header-height + 2*8;
    padding: 8px 0;
  }
}
/* 职位邀请预览样式  */
.invitaion-position{
  padding: 0;
}


/* 人才邀请卡片样式 */
.talent-invitation-container {
  width: 588px;
  display: inline-block;

  .input-container {
    padding: 0;

    textarea {
      padding: 0;
      margin: 0;
    }
  }
  .select-wrapper {

    option {
      font-size: 14px;
      color: rgba(0,0,0,0.54);
    }
  }

  .content-action {
    position: relative;

    .option-action {
      display: inline-block;
      padding-left: 24px;
      width: 150px;
      line-height: 1.5;

      a {
        cursor: pointer;
        display: inline-block;
        width: 100%;
        color: #13A277;
      }
    }

    .hard-action {
      display: inline-block;
      position: absolute;
      right: 16px;
      top: 16px;
    }
  }

}

#save-for-modal {
  min-width: 100px;
  width: 500px;
}


#position-inv-progress-modal {
  min-width: 100px;
  width: 720px;

  textarea{
    background: #FFF;
  }
  .block-content {
    padding: 12px;
  }
  .position-section-content {
    width: 600px;
  }
  .help-block {
    color: red;
  }
}
