@import './talent-info-display';
@import "../../components/status-tag";

/* 对jx-list的扩展 */
/* 人才信息的列表 */

.recommend-icon {
  position: absolute;
  right: -30px;
  top: 0;
  transform: rotateZ(45deg);

  .recommend-container {
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 30px 30px 30px;
    border-color: transparent transparent #F1BF3A transparent;
  }
  span {
    position: absolute;
    top: 13px;
    right: 13px;
    display: inline-block;
    width: 30px;
    color: #FFF;
  }
}

.list-header {
  width: 100%;
  height: 24px;
  margin-bottom: 12px;

  .header-item {
    width: 33.33%;
    display: inline-block;

    .user-name {
      font-size: 16px;
      color: $primary-color;
      display: inline-block;
      width: 100px;
      @extend .truncate;
    }
    .sprite-location {
      vertical-align: top;
      margin: 0 2px;
    }
    time {
      display: inline-block;
      font-size: 12px;
      width: 70px;
      color: rgba(0,0,0,0.28);
      margin-left: 8px;
    }
    .mini-state-btn {
      line-height: 24px;
    }
  }
}

.talent-type {

  span.mini-action-btn {
    @extend .talent-type-font;
    background: rgba(17, 162, 121, 1);
  }
}

.talent-skill {

  .skill-item {
    width: 100%;
    padding-bottom: 8px;

    .skill-title {
      display: inline-block;
      vertical-align: top;
      @extend .common-title-font;
      span {
        color: rgba(0,0,0,0.87);
      }
    };

  }
}

.talent-extra {

  .talent-type {
    line-height: 24px;
  }

  .score-item {
    height: 20px;
    margin-bottom: 4px;

    span {
      display: inline-block;
      vertical-align: middle;
      color: rgba(0,0,0,0.87);
    }
  }
  .job-item {

    span {
      color: $font-green-color;
    }

  }
}

.score-bar-container {
  display: inline-block;
  vertical-align: middle;
  margin-left: 12px;
  width: 70px;

  .score-bar {
    background: #FFF;
    height: 8px;
    border-radius: 4px;
    border: 1px solid #d9d9d9;

    .percent {
      background: #FFF;
      height: 7px;
      border-radius: 4px;
      -webkit-transition: background, 0.3s, ease;
    }
  }
}

.i-indicator {
  display: inline-block;
  vertical-align: top;
  margin-right: 16px;
  border: 2px solid $font-green-color;
  width: 2px;
  height: 16px;
}
