/* <!-- 简历页，人才搜索人才简要信息的展示样式 --> */
.personal-info-multiple {
  color: rgba(0,0,0,0.54);
  display: inline-block;
  vertical-align: top;
  width: 170px;

  .info-item {
    display: inline-block;
    width: 100%;
    position: relative;
    height: auto;
    line-height: 24px;
    @extend text-ellipsis;

    &.multiple-skill {
      height: auto;
    }

    span {
      color: rgba(0,0,0,0.54);
      vertical-align: middle;
      @extend .text-ellipsis;
      max-width: 100%;
      margin-right: 6px;
      font-size: 14px;
      line-height: 24px;
    }

    label {
      color: rgba(0,0,0,0.54);
      font-size: 14px;
      position: absolute;
      padding-left: 25px;
      left: 0;
      line-height: 24px;
      width: 160px;
      @extend .text-ellipsis;
    }
  }

  .item-title {
    display: inline-block;
    span {
      color: rgba(0,0,0,0.87);
    }
  }

  .skill-content {
    display: inline-block;
    padding-left: 0;
    vertical-align: top;
    width: 100px;
    p {
      margin-bottom: 8px;
    }
  }
}
