.md-divider{
    border-top-color: rgba(0,0,0,0.12);
    border-top: 1px solid;
    position: absolute;
    width: 100%;
    margin: 0;
    left: 0;
    bottom: 0;
}
.column{
    width: 5em;
    padding-right: 10px;
    background:gray;
}
.md-img{
    max-width: 100%;
    height: auto;
    display: block;
}
.page-size{
    width: 100%;
    height: 600px;
}
.center{
    position: absolute;
    margin: auto;
    left:0;right:0;top:0;bottom:0;
}
.center-pre{
    position: absolute;
    margin: auto;
}
